import request from '@/utils/request';
export function getArticleCategory() {
  return request({
    url: "/api/admin/article/category",
    method: 'get'
  });
}
export function createArticle(data) {
  return request({
    url: '/api/admin/article',
    method: 'post',
    data: data
  });
}
export function getArticleList(params) {
  return request({
    url: '/api/admin/article',
    method: 'get',
    params: params
  });
}
export function updateArticle(id, params) {
  return request({
    url: "/api/admin/article/".concat(id),
    method: 'put',
    params: params
  });
}
export function showArticle(id) {
  return request({
    url: "/api/admin/article/".concat(id),
    method: 'get'
  });
}
export function getLatestArticle() {
  return request({
    url: '/api/admin/article/latest',
    method: 'get'
  });
}